import './App.css';
import React, { useState } from 'react';

function Modify(props){

  let [phMin, setPhMin] = useState('')
  let [phMax, setPhMax] = useState('')
  let [pCO2Min, setPCO2Min] = useState('')
  let [pCO2Max, setPCO2Max] = useState('')
  let [naMin, setNaMin] = useState('')
  let [naMax, setNaMax] = useState('')
  let [kMin, setKMin] = useState('')
  let [kMax, setKMax] = useState('')
  let [clMin, setClMin] = useState('')
  let [clMax, setClMax] = useState('')
  let [hco3Min, setHco3Min] = useState('')
  let [hco3Max, setHco3Max] = useState('')
  let [agMin, setAgMin] = useState('')
  let [agMax, setAgMax] = useState('')
  
  
  return(
    <div className="black-background" onClick={(e)=>{
      if (e.target == e.currentTarget) {
        props.참고치수정모달변경(false);
      };
    }}>
      <div className='modify'>
        <table class="type01">
          <tr>
            <th scope="row">검사항목</th>
            <td>정상 최소치</td>
            <td id="Max">정상 최대치</td>
          </tr>
          <tr>
          <th scope="row" id='pH'>pH</th>
            <td>
              <input type="number" step="1" onChange={(e)=>{setPhMin(e.target.value)}}/>
            </td>
            <td>
              <input type="number" step="1" onChange={(e)=>{setPhMax(e.target.value)}}/>
            </td>
          </tr>
          <tr>
          <th scope="row" id='pH'>P<sub>v</sub>CO2</th>
            <td>
              <input type="number" step="1" onChange={(e)=>{setPCO2Min(e.target.value)}}/>
            </td>
            <td>
              <input type="number" step="1" onChange={(e)=>{setPCO2Max(e.target.value)}}/>
            </td>
          </tr>
          <tr>
          <th scope="row" id='Na'>Na<sup>+</sup></th>
            <td>
              <input type="number" step="1" onChange={(e)=>{setNaMin(e.target.value)}}/>
            </td>
            <td>
              <input type="number" step="1" onChange={(e)=>{setNaMax(e.target.value)}}/>
            </td>
          </tr>
          <tr>
            <th scope="row" id='K'>K<sup>+</sup></th>
            <td>
              <input type="number" step="1" onChange={(e)=>{setKMin(e.target.value)}}/>
            </td>
            <td>
              <input type="number" step="1" onChange={(e)=>{setKMax(e.target.value)}}/>
            </td>
          </tr>
          <tr>
          <th scope="row" id='Cl'>Cl<sup>-</sup></th>
            <td>
              <input type="number" step="1" onChange={(e)=>{setClMin(e.target.value)}}/>
            </td>
            <td>
              <input type="number" step="1" onChange={(e)=>{setClMax(e.target.value)}}/>
            </td>
          </tr>
          <tr>
          <th scope="row" id='HCO3'>HCO3<sup>-</sup></th>
            <td>
              <input type="number" step="1" onChange={(e)=>{setHco3Min(e.target.value)}}/>
            </td>
            <td>
              <input type="number" step="1" onChange={(e)=>{setHco3Max(e.target.value)}}/>
            </td>
          </tr>
          <tr>
            <th scope="row" id='AG'>AG</th>
            <td>
              <input type="number" step="1" onChange={(e)=>{setAgMin(e.target.value)}}/>
            </td>
            <td>
              <input type="number" step="1"  onChange={(e)=>{setAgMax(e.target.value)}}/>
            </td>
          </tr>
        </table>
        <button className='btn btn-success' onClick={()=>{
          props.참고치수정모달변경(false);
          props.참고범위최소변경([`${phMin}`,`${pCO2Min}`,`${naMin}`,`${kMin}`,`${clMin}`,`${hco3Min}`,`${agMin}`])
          props.참고범위최대변경([`${phMax}`,`${pCO2Max}`,`${naMax}`,`${kMax}`,`${clMax}`,`${hco3Max}`,`${agMax}`])
        }}>수정</button>
        <button className='btn btn-danger' onClick={()=>{
          props.참고치수정모달변경(false)
        }}>취소</button>
      </div>
    </div>
  )
}

export default Modify