import React, { useEffect, useState } from 'react';
import { Button } from 'react-bootstrap';
import { useHistory } from 'react-router-dom';
import './Update.scss';
import axios from 'axios';
import {connect} from 'react-redux';

function Update(props){

  let [inputData, inputData변경] = useState('')
  let history = useHistory();
  let [해야할일, 해야할일변경] = useState(['산혈증 알칼리혈증 함수','호흡성, 대사성 구분 함수','보상 개념','AG증가 대사성산증, AG 정상 대사성 산증', 'Delta 비율']);
  let [nothing, nothing변경] = useState(false)

  let [alert, alert변경] = useState(false); //useEffect위에 있어야
  //useEffect함수 : 컴포넌트 생애주기에서 mount되었을 때, update가 되었을 때, 사라질 때 등.. 특정 코드를 실행할 수 있음
  useEffect(()=>{
    let 타이머 = setTimeout(()=>{
      alert변경(true);
    }, 2000);
    return ()=>{clearTimeout(타이머)}//return은 컴퍼넌트가 unmount될 때 실행할 코드임.
  }, [alert]);//Update컴퍼넌트가 재랜더링 될 때 마다 useEffect함수가 실행된다. 이 것은 매우 심한 자원낭비임
              //[alert] : alert라는 스테이트가 변경될 때만 useEffect를 실행해 주세요.
              //빈칸으로 둔다면?? 빈state의 변경이란 건 있을 수 없기 때문에 첫 로딩 후 아무것도 실행 x경우 사용

  let [애니매이션, set애니매이션] = useState(false);


  // 리덕스 연습
  useEffect(()=>{
    console.log(props.자유작명); 
  },[props.자유작명]); //props.자유작명 state update가 될 경우 useEffect로 함수실행


  return (
    <div className="contain">
      {
        alert === true
        ? <div>
          {
            props.alert닫기 === true
            ? <p className="alert">집중!</p>
            : null
          }
            <button className='btn btn-info' onClick={()=>{ props.dispatch({type : '닫기'})}}>집중했어!</button>
          </div>
        : null
      }
      <ul className='toDoList'>
        {
          해야할일.map((a,i)=>{
            return (
            <li className='toDoAndButton' key={i}>
              <span>{a}</span>
              <Button variant="warning" onClick={()=>{
                let done = 해야할일.splice(i, 1);
                nothing변경(!nothing);
                set애니매이션(false)
              }}>Done</Button>
            </li>
            )
          })
        }
      </ul>
      <div className="toDoAddEl">
        <input className='toDoInput' placeholder='To do 추가' onChange={ (e)=>{ inputData변경(e.target.value) }}/>
        <Button variant="success" onClick={()=>{
          let newDo = 해야할일.unshift(inputData);
          nothing변경(!nothing)
        }}>등록</Button>
      </div>
      <Button variant="primary" className='back' onClick={()=>{
        axios.get('https://codingapple1.github.io/shop/data2.json')
        .then((URLdata)=>{     //.then은 요청 성공시 실행할 코드
          let title = URLdata.data.map((a)=>{return a.title});
          title[0] = '기계 selet 기능';
          title[1] = '혼합요인';
          title[2] = '애니매이션';
          let newArray = [...해야할일, ...title];
          해야할일변경(newArray)
        })
        .catch(()=>{})//요청 실패시 실행할 코드
      }} >더보기</Button>
      <Button variant="danger" onClick={()=>{ history.goBack() }} className='back'>뒤로가기</Button>
      {/* history.push('/') 해당 경로로 이동 */}
      <div className={
        애니매이션 === true
        ? 'fadeIn'
        : null
      }>호우</div>
      <button onClick={()=>{set애니매이션(true)}}>애니</button>
      <button onClick={()=>{ props.dispatch({type : '호두출동'})}}>리덕스연습(state값변경)</button>
      <button onClick={()=>{ props.dispatch({type : '메리추가'})}}>리덕스연습(변경값 미리 지정)</button>
      <button onClick={()=>{ props.dispatch({type : '율미추가', payload : {id : 3, name : '율미', 나이 : 25} })}}>리덕스연습(변경값 payload로 전송)</button>
      
  </div>  
  )
};

function state를props화(state){
  return {
    자유작명 : state.reducer,
    alert닫기 : state.reducer2
  }
}

export default connect(state를props화)(Update);
// export default Update