/*eslint-disable*/
import favicon from './favicon.png' 
import { useEffect, useState } from 'react';
import './App.css';
import { Navbar, Container, Nav, NavDropdown } from 'react-bootstrap';
import 멍데이터 from './Datadog';
import 냥데이터 from './Datacat'
import { Link, Route, Switch } from 'react-router-dom';
import Update from './Update.js'
import Modify from './Modify.js'
import First from './First.js'
import Second from './Second.js'
import NormalpH from './NormalpH.js'
import Third from './Third.js'
import Third_1 from './Third_1.js'
import Forth from './Forth.js'
import { render } from '@testing-library/react';

function App() {

  let [dogdata, dogdata변경] = useState(멍데이터)
  let [catdata, catdata변경] = useState(냥데이터)
  const dogMinData = dogdata.map((a)=>{
    return a.min
  })
  const dogMedData = dogdata.map((a)=>{
    return a.med
  })
  const dogMaxData = dogdata.map((a)=>{
    return a.max
  })
  const catMinData = catdata.map((a)=>{
    return a.min
  })
  const catMaxData = catdata.map((a)=>{
    return a.max
  })


  let[참고범위최소, 참고범위최소변경] = useState(['m','m','m','m','m','m','m']);
  let[참고범위최대, 참고범위최대변경] = useState(['M','M','M','M','M','M','M']);
  
  let[pHinput, pHinput변경] = useState('')
  let[PCO2input, PCO2input변경] = useState('')
  let[Nainput, Nainput변경] = useState('');
  let[Kinput, Kinput변경] = useState('');
  let[Clinput, Clinput변경] = useState('');
  let[HCO3input, HCO3input변경] = useState('');
  let[AG, AG변경] = useState('계산값')

  let[참고치수정모달, 참고치수정모달변경] = useState(false);
  let[pH분석, setpH분석] = useState(false);
  let[pCO2분석, setpCO2분석] = useState(false);
  let[정상pH, set정상pH] = useState(false)
  let[보상분석, set보상분석] = useState(false)
  let[보상x분석, set보상x분석] = useState(false)
  let[AG분석, setAG분석] = useState(false)
  let[보상중, set보상중] = useState(false)
  let[보상x, set보상x] = useState(false)
  let[양상, set양상] = useState('')

  function 그래프높이조절 () {
    document.querySelector('.Na').style.height = `${2*Nainput}px`;
    document.querySelector('.Na').style.lineHeight = `${2*Nainput}px`;
    document.querySelector('.K').style.height = `${2*Kinput}px`;
    document.querySelector('.K').style.lineHeight = `${2*Kinput}px`;
    document.querySelector('.Cl').style.height = `${2*Clinput}px`;
    document.querySelector('.Cl').style.lineHeight = `${2*Clinput}px`;
    document.querySelector('.HCO3').style.height = `${2*HCO3input}px`;
    document.querySelector('.HCO3').style.lineHeight = `${2*HCO3input}px`;
    document.querySelector('.AG').style.height = `${2*(Number(Nainput)+Number(Kinput)-Number(Clinput)-Number(HCO3input))}px`  
    document.querySelector('.AG').style.lineHeight = `${2*(Number(Nainput)+Number(Kinput)-Number(Clinput)-Number(HCO3input))}px`  
  }
  
  // 증감판독기
  function pH증감판독기(){
    if(pHinput == ''){
      return ''
    } else if(pHinput < 참고범위최소[0]){
      return '🔽'
    } else if (pHinput > 참고범위최대[0]){
      return '🔼'
    }
  }
  function pCO2증감판독기(){
    if(PCO2input == ''){
      return ''
    } else if(PCO2input < dogMedData[1]){
      return '🔽'
    } else if (PCO2input > dogMedData[1]){
      return '🔼'
    }
  }
  function Na증감판독기(){
    if(Nainput == ''){
      return ''
    } else if(Nainput < 참고범위최소[2]){
      return '🔽'
    } else if (Nainput > 참고범위최대[2]){
      return '🔼'
    }
  }
  function K증감판독기(){
    if(Kinput == ''){
      return ''
    } else if(Kinput < 참고범위최소[3]){
      return '🔽'
    } else if (Kinput > 참고범위최대[3]){
      return '🔼'
    }
  }
  function Cl증감판독기(){
    if(Clinput == ''){
      return ''
    } else if(Clinput < 참고범위최소[4]){
      return '🔽'
    } else if (Clinput > 참고범위최대[4]){
      return '🔼'
    }
  }
  function HCO3증감판독기(){
    if(HCO3input == ''){
      return ''
    } else if(HCO3input < dogMedData[5]){
      return '🔽'
    } else if (HCO3input > dogMedData[5]){
      return '🔼'
    }
  }
  function AG증감판독기(){
    if(AG == '계산값'){
      return ''
    } else if (AG > 참고범위최대[6]){
      return '🔼'
    }
  }

  function 혈액산도판독기(){
    if ( pHinput < 참고범위최소[0]) {
      return '산혈증'
    } else if (pHinput > 참고범위최대[0]) {
      return '알칼리혈증'
    } else {
      return '정상'
    }
  }
  
  function pCO2판독기(){
    if(혈액산도판독기() === '산혈증'){
      if (PCO2input < ((Number(참고범위최소[1])+Number(참고범위최대[1]))/2)){
        return '대사성산증'
      } else {
        return '호흡성산증'
      }
    } else if (혈액산도판독기() === '알칼리혈증'){
      if (PCO2input > ((Number(참고범위최소[1])+Number(참고범위최대[1]))/2)){
        return '대사성알칼리증'
      } else {
        return '호흡성알칼리증'
      }
    } 
  }

  function 보상판독기(){
    if (pCO2판독기() === '대사성산증'){
      if ( 38.5-Number(PCO2input)-0.7*(24.5-Number(HCO3input)) <= 2 && 38.5-Number(PCO2input)-0.7*(24.5-Number(HCO3input)) >= -2){
        return '보상중'
      } else {
        return '보상x'
      }
    } else if (pCO2판독기() === '대사성알칼리증'){
      if (Number(PCO2input)-38.5-0.7*(Number(HCO3input)-24.5) <= 2 && Number(PCO2input)-38.5-0.7*(Number(HCO3input)-24.5) >= -2){
        return '보상중'
      } else {
        return '보상x'
      }
    } else if (pCO2판독기() === '호흡성산증'){
      return <select onChange={(e)=>{
        if (e.target.value === '급성') {
          if (Number(HCO3input)-24.5-0.15*(Number(PCO2input)-38.5) <= 2 && Number(HCO3input)-24.5-0.15*(Number(PCO2input)-38.5) >= -2) {
            set보상중(true);
            set보상x(false);
            set양상('급성')
          } else {
            set보상x(true);
            set보상중(false);
            set양상('급성')
          }
        } else if (e.target.value === '만성'){
          if (Number(HCO3input)-24.5-0.35*(Number(PCO2input)-38.5) <= 2 && Number(HCO3input)-24.5-0.35*(Number(PCO2input)-38.5) >= -2) {
            set보상중(true);
            set보상x(false);
            set양상('만성')
          } else {
            set보상중(false);
            set보상x(true);
            set양상('만성')
          }
        }
        }}>
        <option>양상</option>
        <option>급성</option>
        <option>만성</option>
      </select>
    } else if (pCO2판독기() === '호흡성알칼리증'){
      return <select onChange={(e)=>{
        if (e.target.value === '급성') {
          if (Number(HCO3input)-24.5+0.25*(38.5-Number(PCO2input)) <= 2 && Number(HCO3input)-24.5+0.25*(38.5-Number(PCO2input)) >= -2) {
            set보상중(true);
            set보상x(false);
            set양상('급성')
          } else {
            set보상x(true);
            set보상중(false);
            set양상('급성')
          }
        } else if (e.target.value === '만성'){
          if (Number(HCO3input)-24.5+0.55*(Number(PCO2input)-38.5) <= 2 && Number(HCO3input)-24.5+0.55*(Number(PCO2input)-38.5) >= -2) {
            set보상중(true);
            set보상x(false);
            set양상('만성')
          } else {
            set보상중(false);
            set보상x(true);
            set양상('만성')
          }
        }
        }}>
      <option>양상</option>
      <option>급성</option>
      <option>만성</option>
    </select>
    }
  }
  function 보상x판독기(){
    if(pCO2판독기() === '대사성산증'){
      if(38.5-Number(PCO2input)-0.7*(24.5-Number(HCO3input)) > 2){
        return 'mixed 대사성알칼리증'
      } else if (38.5-Number(PCO2input)-0.7*(24.5-Number(HCO3input)) < -2){
        return 'mixed 호흡성산증 or 또 다른 대사성 산증'
      }
    } else if(pCO2판독기() === '대사성알칼리증'){
      if(Number(PCO2input)-38.5-0.7*(Number(HCO3input)-24.5) > 2){
        return 'mixed 대사성산증'
      } else if (Number(PCO2input)-38.5-0.7*(Number(HCO3input)-24.5) < -2){
        return 'mixed 호흡성 알칼리증 or 또 다른 대사성 알칼리증'
      }
    } else if(pCO2판독기() === '호흡성산증'){
      if (양상 === '급성'){
        if(Number(HCO3input)-24.5+0.25*(38.5-Number(PCO2input)) > 2){
          return 'mixed 대사성알칼리증'
        } else if(Number(HCO3input)-24.5+0.25*(38.5-Number(PCO2input)) < -2){
          return 'mixed 대사성산증'
        }
      } else if (양상 === '만성'){
        if(Number(HCO3input)-24.5-0.35*(Number(PCO2input)-38.5) > 2){
          return 'mixed 대사성알칼리증'
        } else if (Number(HCO3input)-24.5-0.35*(Number(PCO2input)-38.5) < -2){
          return 'mixed 대사성산증'
        }
      }
    } else if(pCO2판독기() === '호흡성알칼리증'){
      if (양상 === '급성'){
        if(Number(HCO3input)-24.5+0.25*(38.5-Number(PCO2input)) < -2){
          return 'mixed 대사성산증'
        } else if(Number(HCO3input)-24.5+0.25*(38.5-Number(PCO2input)) > 2){
          return 'mixed 대사성알칼리증'
        }
      } else if(양상 === '만성'){
        if(Number(HCO3input)-24.5+0.55*(Number(PCO2input)-38.5) > 2){
          return 'mixed 대사성알칼리증'
        } else if(Number(HCO3input)-24.5+0.55*(Number(PCO2input)-38.5) < -2){
          return 'mixed 대사성산증'
        }
      }
    }
  }


  function AG판독기(){
    if (AG > 20) {
      return '대사성 산증이 하나라도 있는 경우 "High AG" Metabolic Acidosis'
    } else {
      return '대사성 산증이 하나라도 있는 경우"Normal AG" Metabolic Acidosis'
    }
  }

  
    // console.log('--------------------------구분선----------------')
    // console.log(Number(HCO3input)-24.5+0.55*(Number(PCO2input)-38.5))
    // console.log(Number(HCO3input)-24.5+0.55*(Number(PCO2input)-38.5) > 2)
    // console.log(Number(HCO3input)-24.5+0.55*(Number(PCO2input)-38.5) < -2)
    // console.log(보상중)
    // console.log(보상x)
    // console.log(양상)

  return (
    <div className="App">

      {/* NAVBAR */}
      <Navbar collapseOnSelect expand="lg" bg="dark" variant="dark">
        <Container>
        <Navbar.Brand><Link to="/" className='noDeco'>VBGA analyzer</Link></Navbar.Brand>
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav">
          <Nav className="me-auto">
            <Nav.Link href="#features">사용법</Nav.Link>
            <Nav.Link><Link to="/update" className='noDeco'>업데이트</Link></Nav.Link>
            <NavDropdown title="more" id="collasible-nav-dropdown">
              <NavDropdown.Item href="#action/3.1">임시</NavDropdown.Item>
              <NavDropdown.Divider />
              <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
            </NavDropdown>
          </Nav>
          <Nav>
            <Nav.Link href="#deets">임시</Nav.Link>
            <Nav.Link eventKey={2} href="#memes">
              Ver 1.0.0
            </Nav.Link>
          </Nav>
        </Navbar.Collapse>
        </Container>
      </Navbar>

      <Route exact path="/">
      <div className='selection'>
          <select className='species' onChange={(e)=>{
            if (e.target.value === '개') {
              참고범위최소변경(dogMinData);
              참고범위최대변경(dogMaxData);
            } else if (e.target.value === '고양이') {  
              참고범위최소변경(catMinData);
              참고범위최대변경(catMaxData);
            }}}>
            <option>종선택</option>
            <option>개</option>
            <option>고양이</option>
          </select>
      </div>

      <div className="tableAndGraph">

        {/* TABLE */}
        <table class="type01">
          <tr>
            <th scope="row">검사항목</th>
            <td>참고범위
              <button onClick={()=>{참고치수정모달변경(true)}} className='btn btn-success'>수정</button>
            </td>
            <td>입력</td>
            <td>증감</td>
          </tr>
          <tr>
            <th scope="row" id='pH'>pH</th>
            <td>{참고범위최소[0]} ~ {참고범위최대[0]}</td>
            <td>
              <input type="number" step="1" onChange={(e)=>{pHinput변경(e.target.value)}}/>
            </td>
            <td> {pH증감판독기()}</td>
          </tr>
          <tr>
            <th scope="row" id='pH'>P<sub>v</sub>CO2</th>
            <td>{참고범위최소[1]} ~ {참고범위최대[1]} mmHg</td>
            <td>
              <input type="number" step="1" onChange={(e)=>{PCO2input변경(e.target.value)}}/>
            </td>
            <td>{pCO2증감판독기()}</td>
          </tr>
          <tr>
            <th scope="row" id='Na'>Na<sup>+</sup></th>
            <td>{참고범위최소[2]} ~ {참고범위최대[2]} mmol/L</td>
            <td>
              <input type="number" step="1" onChange={(e)=>{Nainput변경(e.target.value)}}/>
            </td>
            <td>{Na증감판독기()}</td>
          </tr>
          <tr>
            <th scope="row" id='K'>K<sup>+</sup></th>
            <td>{참고범위최소[3]} ~ {참고범위최대[3]} mmol/L</td>
            <td>
              <input type="number" step="1" onChange={(e)=>{Kinput변경(e.target.value)}}/>
            </td>
            <td>{K증감판독기()}</td>
          </tr>
          <tr>
            <th scope="row" id='Cl'>Cl<sup>-</sup></th>
            <td>{참고범위최소[4]} ~ {참고범위최대[4]} mmol/L</td>
            <td>
              <input type="number" step="1" onChange={(e)=>{Clinput변경(e.target.value)}}/>
            </td>
            <td>{Cl증감판독기()}</td>
          </tr>
          <tr>
            <th scope="row" id='HCO3'>HCO3<sup>-</sup></th>
            <td>{참고범위최소[5]} ~ {참고범위최대[5]} mmol/L</td>
            <td>
              <input type="number" step="1" onChange={(e)=>{HCO3input변경(e.target.value)}}/>
            </td>
            <td>{HCO3증감판독기()}</td>
          </tr>
          <tr>
            <th scope="row" id='AG'>AG</th>
            <td>{참고범위최소[6]} ~ {참고범위최대[6]} mmol/L</td>
            <td>{AG}</td>
            <td>{AG증감판독기()}</td>
          </tr>
        </table>

        {/* 막대그래프 */}
          <div class="graph">
            <div class="plus">
              <div class="Na">Na</div>
              <div class="K">K</div>
            </div>
            <div class="minus">
              <div class="AG">AG</div>
              <div class="HCO3">HCO3</div>
              <div class="Cl">Cl</div>
            </div>  
          </div>


        
        
      </div>

      {/* Steps, Reset버튼 */}
      <div className='anal-btn'>
        <button onClick={()=>{
                  AG변경(Number(Nainput)+Number(Kinput)-Number(Clinput)-Number(HCO3input));
                  그래프높이조절();
                  setpH분석(true);
                  }} className='btn btn-primary'>Steps
        </button>
        <button onClick={()=>{
            location.reload() //새로고침 기능 
          }} className='btn btn-danger'>Reset</button> 
      </div>
      </Route>
      
      {/* 참고범위 수정 컴퍼넌트 */}
      {
        참고치수정모달 === true
        ? <Modify 참고치수정모달변경={참고치수정모달변경} 참고범위최소변경={참고범위최소변경} 참고범위최대변경={참고범위최대변경} 참고범위최소={참고범위최소} 참고범위최대={참고범위최대}/>
        : null
      }
      {/* 분석기들 */}
      <div className='steps'>
      {
        pH분석 === true
        ? <First 혈액산도판독기={혈액산도판독기} setpCO2분석={setpCO2분석} set정상pH={set정상pH}/>
        : null
      }
      {
        pCO2분석 === true
        ? <Second pCO2판독기={pCO2판독기} set보상분석={set보상분석}/>
        : null
      }
      {
        보상분석 === true
        ? <Third 보상판독기={보상판독기} 보상중={보상중} set보상중={set보상중} 보상x={보상x} set보상x={set보상x} setAG분석={setAG분석} set보상x분석={set보상x분석}/>
        : null
      }
      {
        보상x분석 === true
        ? <Third_1 보상x판독기={보상x판독기} setAG분석={setAG분석}/>
        :null
      }
      {     
        AG분석 === true
        ? <Forth AG판독기={AG판독기}/>
        : null
      }
      </div>
      {
        정상pH === true
        ? <NormalpH />
        : null
      }

      
      
      
      
      
      
      <Route path="/update">
        <Update />
      </Route>







      <div class="help-tip">
        <p>호버시 메세지</p>
      </div>
      
    </div>
  );
}

export default App;
