import './App.css';

function First (props){

  return(
    <div className="box pH">
      <p>1단계 : pH 분석</p>
      {props.혈액산도판독기()}
      <button className='btn btn-light' onClick={()=>{
        if (props.혈액산도판독기() === '산혈증'){
          props.setpCO2분석(true);
          props.set정상pH(false);
        } else if (props.혈액산도판독기() === '알칼리혈증'){
          props.setpCO2분석(true);
          props.set정상pH(false);
        } else {
          props.setpCO2분석(false);
          props.set정상pH(true);
        }
      }}>다음</button>
    </div>
  )
}

export default First