import './App.css';

function Third (props){

  
  
  return(
    <div className="box compen">
      <p>3단계 : 보상반응 평가</p>
      {props.보상판독기()}
      {
        props.보상판독기() === '보상중'
        ? <button className='btn btn-light' onClick={()=>{
          props.setAG분석(true)
          props.set보상x분석(false)
        }}>다음</button>
        : null
      }
      {
        props.보상판독기() === '보상x'
        ? <button className='btn btn-light' onClick={()=>{
          props.set보상x분석(true)
          props.setAG분석(false)
        }}>다음</button>
        : null
      }
      {
        props.보상중 === true
        ? <div>
            <p>보상중</p>
            <button className='btn btn-light' onClick={()=>{
          props.setAG분석(true)
          props.set보상x분석(false)
        }}>다음</button>
          </div>
        : null
      }
      {
        props.보상x === true
        ? <div>
        <p>보상x</p>
        <button className='btn btn-light' onClick={()=>{
          props.set보상x분석(true)
          props.setAG분석(false)
        }}>다음</button>
      </div>
        : null
      }
    </div>
  )
}

export default Third